/**SASS de la pagina***/
/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/
body {
  background: #fff;
  color: #666666;
  font-family: "Open Sans", sans-serif; }

a {
  color: #03C4EB;
  transition: 0.5s; }

a:hover, a:active, a:focus {
  color: #03c5ec;
  outline: none;
  text-decoration: none; }

p {
  padding: 0;
  margin: 0 0 30px 0; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  margin: 0 0 20px 0;
  padding: 0; }

/* Prelaoder */
#preloader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #fff url("../img/preloader.svg") no-repeat center center; }

/* Back to top button */
.back-to-top {
  position: fixed;
  display: none;
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
  padding: 6px 12px 9px 12px;
  font-size: 16px;
  border-radius: 2px;
  right: 15px;
  bottom: 15px;
  transition: background 0.5s; }
  @media (max-width: 768px) {
    .back-to-top {
      bottom: 15px; } }
  .back-to-top:focus {
    background: rgba(0, 0, 0, 0.2);
    color: #fff;
    outline: none; }
  .back-to-top:hover {
    background: #03C4EB;
    color: #fff; }

/*--------------------------------------------------------------
# Welcome
--------------------------------------------------------------*/
#hero {
  display: table;
  width: 100%;
  height: 100vh;
  background: url(../img/hero-bg.jpg) top center fixed;
  background-size: cover; }
  #hero .hero-logo {
    margin: 2rem; }
    #hero .hero-logo img {
      max-width: 100%; }
  #hero .hero-container {
    background: rgba(0, 0, 0, 0.596);
    display: table-cell;
    margin: 0;
    padding: 0;
    text-align: center;
    vertical-align: middle; }
  #hero h1 {
    margin: 30px 0 10px 0;
    font-weight: 700;
    line-height: 48px;
    text-transform: uppercase;
    color: #fff; }
    @media (max-width: 768px) {
      #hero h1 {
        font-size: 28px;
        line-height: 36px; } }
  #hero h2 {
    color: #999;
    margin-bottom: 50px; }
    @media (max-width: 768px) {
      #hero h2 {
        font-size: 24px;
        line-height: 26px;
        margin-bottom: 30px; }
        #hero h2 .rotating {
          display: block; } }
  #hero .rotating {
    font-weight: 600;
    color: #ffffff56; }
  #hero .rotating > .animated {
    display: inline-block; }
  #hero .actions a {
    font-family: "Raleway", sans-serif;
