/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
#header {
  position: -webkit-sticky;
  /* Safari */
  position: sticky;
  top: 0;
  background: #000000;
  padding: 20px 0;
  height: 80px;
  z-index: 20; }
  #header a {
    font-size: 1rem;
    font-family: "Lato", sans-serif; }
  #header #logo {
    float: left; }
    #header #logo h1 {
      font-size: 36px;
      margin: 0;
      padding: 6px 0;
      line-height: 1;
      font-family: "Raleway", sans-serif;
      font-weight: 700;
      letter-spacing: 3px;
      text-transform: uppercase; }
    #header #logo h1 a, #header #logo h1 a:hover {
      color: #fff; }
