      padding: 0;
      margin: 1rem 0rem 0 2rem;
      max-height: 50px; }
  @media (max-width: 768px) {
    #header {
      height: 80px; }
      #header #logo h1 {
        font-size: 26px; }
      #header #logo img {
        max-height: 40px; } }
  .is-sticky #header {
    background: #000000; }

/*--------------------------------------------------------------
# Navigation Menu
--------------------------------------------------------------*/
/* Nav Menu Essentials */
.nav-menu {
  margin-right: 5rem; }
  .nav-menu * {
    margin: 0;
    padding: 0;
    list-style: none; }
  .nav-menu ul {
    position: absolute;
    visibility: hidden;
    top: 100%;
    left: 0;
    z-index: 99; }
  .nav-menu li {
    position: relative;
    white-space: nowrap; }
  .nav-menu > li {
    float: left; }
  .nav-menu li:hover > ul {
    overflow: hidden;
    visibility: visible;
    -moz-transition: 0.001s;
    -ms-transition: 0.01s;
    -o-transition: 0.01s;
    -webkit-transition: 0.001s;
    transition: 0.002s; }
  .nav-menu ul ul {
    top: 0;
    left: 100%; }
  .nav-menu ul li {
    min-width: 180px; }

/* Nav Menu Arrows */
.sf-arrows .sf-with-ul {
  padding-right: 30px; }

.sf-arrows .sf-with-ul:after {
  content: "\f107";
  position: absolute;
  right: 15px;
  font-family: FontAwesome;
  font-style: normal;
  font-weight: 700; }

.sf-arrows ul .sf-with-ul:after {
  content: "\f105"; }

/* Nav Meu Container */
#nav-menu-container {
  float: right;
  margin: 0.5rem 0.8rem 0.5rem 0; }
  @media (max-width: 900px) {
    #nav-menu-container {
      display: none; } }

/* Nav Meu Styling */
.nav-menu a {
  padding: 10px 15px;
  text-decoration: none;
  display: inline-block;
  color: #fcfcfcde;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-size: 14px;
  outline: none; }

.nav-menu a:hover, .nav-menu li:hover > a, .nav-menu .menu-active > a {
  color: #03C4EB; }

.nav-menu ul {
  margin: 4px 0 0 15px;
  box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.08); }
  .nav-menu ul li {
    background: #fff;
    border-top: 1px solid #f4f4f4; }
    .nav-menu ul li:first-child {
      border-top: 0; }
    .nav-menu ul li:hover {
      background: #f6f6f6; }
    .nav-menu ul li a {
      color: #33333375; }
  .nav-menu ul ul {
    margin: 0; }

/* Mobile Nav Toggle */
#mobile-nav-toggle {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 999;
  margin: 20px 20px 0 0;
  border: 0;
  background: none;
  font-size: 24px;
  display: none;
  transition: all 0.06s;
  outline: none; }
  #mobile-nav-toggle i {
    color: #fff; }
  @media (max-width: 870px) {
    #mobile-nav-toggle {
      display: inline;
      cursor: pointer; } }

/* Mobile Nav Styling */
#mobile-nav {
  position: fixed;
  top: 0;
  padding-top: 18px;
  bottom: 0;
  z-index: 998;
  background: rgba(3, 1, 1, 0.568);
  left: -260px;
  width: 260px;
  overflow-y: auto;
  transition: all 0.6s; }
  #mobile-nav ul {
    padding: 0;
    margin: 0;
    list-style: none; }
    #mobile-nav ul li {
      position: relative; }
      #mobile-nav ul li a {
        color: #ffffffe7;
        font-size: 16px;
        overflow: hidden;
        padding: 10px 22px 10px 15px;
        position: relative;
        text-decoration: none;
        width: 100%;
        display: block;
        outline: none;
        font-weight: 500;
        z-index: 50; }
        #mobile-nav ul li a:hover {
          color: #fdfdfd5e; }
      #mobile-nav ul li li {
        padding-left: 30px; }
    #mobile-nav ul .menu-has-children i {
      position: absolute;
      right: 0;
      z-index: 99;
      padding: 15px;
      cursor: pointer;
      color: #fff; }
      #mobile-nav ul .menu-has-children i.fa-chevron-up {
        color: #03C4EB; }
    #mobile-nav ul .menu-item-active {
      color: #03C4EB; }

#mobile-body-overly {
  width: 100%;
  height: 100%;
  z-index: 997;
  top: 0;
  left: 0;
  position: fixed;
  background: rgba(0, 0, 0, 0.6);
  display: none; }

/* Mobile Nav body classes */
body.mobile-nav-active {
  overflow: hidden; }
  body.mobile-nav-active #mobile-nav {
    left: 0; }
  body.mobile-nav-active #mobile-nav-toggle {
    color: #fff; }

.menu-has-children:hover .menu-desplegado {
  display: block; }

